      // https://stackoverflow.com/questions/44012261/getting-around-cors-with-embedded-google-forms
      // set the target on the form to point to a hidden iframe
      // some browsers need the target set via JavaScript, no idea why...
      document.getElementById("custom-google-form").target =
        "form-response-iframe";
      // detect when the iframe reloads
      var iframe = document.getElementById("form-response-iframe");
      if (iframe) {
        iframe.onload = () => {
          this.showModal();
        };
      }
    },
    showModal() {
      let successModal = new Modal(document.getElementById("success-modal"));
      successModal.show();
    },
    createIframe() {
      let iframe = document.createElement("iframe");
      iframe.style = "position: absolute; width:0; height:0; border:0;";
      iframe.name = "form-response-iframe";
      iframe.id = "form-response-iframe";
      document.body.appendChild(iframe);
      return iframe;
    },
  },
